@import "resets.scss";
@import "normalizes.scss";

* {
    box-sizing: border-box; }

@font-face {
    font-family: "RobotO";
    src: url('../fonts/Roboto-Regular.ttf');
    font-style: normal;
    font-weight: normal; }
@font-face {
    font-family: "RobotO";
    src: url('../fonts/Roboto_bolt.ttf');
    font-weight: bold; }

html {
    font-family: "RobotO"; }

$color-grey: #747474;

.fist-section {
    margin-top: 35px;
    margin-bottom: 60px;
    &__header {
        display: flex;
        justify-content: space-between;
        margin-bottom: 115px;
        margin-left: 72px; }
    &__info {
        padding-top: 10px;
        font-size: 18px;
        line-height: 2;
        span {
            color: $color-grey; } } }

.nav-menu {
    &__list {
        display: flex;
        flex-direction: row; }
    &__item {
        font-size: 20px;
        font-weight: 700;
        margin-right: 20px;
        &:last-child {
            margin-right: 0px; }
        a {
            text-decoration: none;
            color: black; } } }

.container {
    max-width: 1140px;
    margin: 0 auto; }

.center-box {
    width: 1140px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 110px;
    &__img {
        width: 570px; } }

.title {
    font-size: 45px;
    font-weight: bold;
    span {
        font-size: 40px;
        font-weight: normal; } }

.right-part {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    &__elips {
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: black;
        width: 138px;
        height: 138px;
        border-radius: 69px;
        color: #FFFFFF;
        font-size: 14px;
        text-align: center; } }

.partners-box {
    display: flex;
    flex-direction: column;
    align-items: center;
    &__text {
        font-size: 27px;
        margin-bottom: 47px; }
    span {
        font-size: 22px; } }

.second-section {
    margin-top: 60px;
    margin-bottom: 80px;
    &__content-container {
        display: flex;
        flex-direction: row;
        justify-content: space-between; }
    &__title {
        margin-bottom: 32px; }
    &__text {
        color: $color-grey;
        font-size: 16px; } }
.right-list {
    &__item {
        margin-bottom: 9px;
        &:last-child {
            margin-bottom: 0px; } } }
.list-title {
    margin-top: 78px;
    width: 385px;
    height: 46px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    border-bottom: 1px solid black;
    &__text {
        font-size:  18px;
        font-weight: bold; }
    &__number {
        font-size:  18px;
        font-weight: bold; } }

.awards {
    margin-top: 20px;
    width: 385px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    color: $color-grey;
    font-size: 16px;
    &__number {
        display: flex;
        flex-direction: column;
        align-items:  flex-end; } }

.third-section {
    &__title {
        text-align: center;
        margin-bottom: 20px; }
    &__img-box {
        width: 960px;
        margin: 0 auto; } }
.img-box {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    &__right-column {
        margin-top: 117px; }
    &__img {
        margin-bottom: 40px; }
    &__text {
        margin-bottom: 20px;
        width: 470px;
        text-align: center;
        font-size: 22px;
        line-height: 1.5;
        font-weight: bold;
        span {
            font-size: 22px;
            color: $color-grey;
            font-weight: normal; } } }



